.movieItem {
    transform: scale(1);
    transition: all 0.25s linear;
  }
  
  .mainTitle {
    padding: 20px 40px;
    font-size: 30px;
  }
  
  .title {
    font-size: 18px;
    width: 300px;
    margin: 10px 0;
  }
  
  .poster {
    width: 300px;
    display: block;
    border-radius: 3px;
  }
  
  .movieItem:hover,
  .movieItem:focus {
    color: #126b18dc;
    cursor: pointer;
    transform: scale(1.1);
    transition: all 0.25s linear;
  }
  
  .loading {
    display: flex;
    width: 100%;
    height: 100vh;
    background: #7400bc29;
    position: absolute;
    transition: 0.5s;
    top: 0;
    left: 0;
  }